import React, { Component } from 'react';
import posed from 'react-pose';
import './Modal.css';

const ModalBox = posed.div({
  enter: {
    y: '-50%',
    x: '-50%',
    scale: 1,
    opacity: 1,
    transition: {
      scale: { type: 'spring', stiffness: 500, damping: 15 },
      default: { duration: 300 }
    }
  },
  exit: {
    y: '-50%',
    x: '-50%',
    scale: 1.5,
    opacity: 0,
    transition: { duration: 150 }
  }
});

class Modal extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      isNewRecord: false,
      isShown: false
     };
    this.handleClick = this.handleClick.bind(this);
  }
  
  componentDidMount() {
    this.setState({ isShown: true });
  }
  
  componentDidUpdate(prevProps) {
    if (prevProps.gameEnded !== this.props.gameEnded && this.props.gameEnded) {
      this.setState({ isShown: true });
      if (this.defineRecord()) this.setState({ isNewRecord: true });
    }
  }

  handleClick() {
    this.props.onButtonClick();
    this.setState({ isNewRecord: false });
    this.setState({ isShown: false });
  }
  
  getContent() {
    if (this.props.gameEnded) {
      let recordPart = this.state.isNewRecord ? '–≠—Ç–æ, –Ω–∞ —Å–µ–∫—É–Ω–¥–æ—á–∫—É, –Ω–æ–≤—ã–π —Ä–µ–∫–æ—Ä–¥!' : '';
      let pointsPart = this.defineWordEnding(this.props.score.human);
      
      
      if (this.props.score.human >= this.props.finalScore) {
        return {
          title: '–û—á–µ–Ω—å —ç–ø–∏—á–Ω–æ!',
          description: `–ü–æ–±–µ–¥–∞! –¢–µ–±–µ —É–¥–∞–ª–æ—Å—å –Ω–∞–±—Ä–∞—Ç—å ${this.props.finalScore} –±–∞–ª–ª–æ–≤ –≤ "–ì–æ—Ä–æ–¥–∞" –±—ã—Å—Ç—Ä–µ–µ –∫–æ–º–ø—å—é—Ç–µ—Ä–∞. –°–æ–≤–ø–∞–¥–µ–Ω–∏–µ?`,
          buttonText: '–ù–µ –¥—É–º–∞—é.'
        }
      } else if (this.props.score.computer >= this.props.finalScore) {
        return {
          title: '–≠—Ç–æ —Ñ–∏–∞—Å–∫–æ.',
          description: `–•–æ—Ä–æ—à–∞—è –ø–æ–ø—ã—Ç–∫–∞, –Ω–æ –∫–æ–º–ø—å—é—Ç–µ—Ä –Ω–∞–±—Ä–∞–ª ${this.props.finalScore} –±–∞–ª–ª–æ–≤ –±—ã—Å—Ç—Ä–µ–µ.`,
          buttonText: '–°–µ–π—á–∞—Å –ø–æ–±–µ–¥–∏–º!'
        }
      } else {
        return {
          title: '–í—Å–µ, –ø—Ä–∏–µ—Ö–∞–ª–∏!',
          description: <React.Fragment>–Ø –Ω–∞—Å—á–∏—Ç–∞–ª <strong>{this.props.score.human}</strong> –±–∞–ª–ª{pointsPart}. {recordPart}<br/>
          –ñ–µ–ª—Ç–∞—è –∫–Ω–æ–ø–∫–∞ –Ω–∏–∂–µ –ø—Ä–∏–≥–ª–∞—à–∞–µ—Ç –ø–æ–≤—Ç–æ—Ä–∏—Ç—å.</React.Fragment>,
          buttonText: '–ï—â–µ —Ä–∞–∑–æ–∫.'
        }
      }
    }
    
    return {
      title: '–ù–æ–≤–∞—è –∏–≥—Ä–∞',
      description: <React.Fragment>–ü—Ä–∞–≤–∏–ª–∞ –æ—á–µ–Ω—å –ø—Ä–æ—Å—Ç—ã–µ ‚Äî –Ω–∞–∑—ã–≤–∞–π —Ä–µ–∞–ª—å–Ω–æ —Å—É—â–µ—Å—Ç–≤—É—é—â–∏–µ –≥–æ—Ä–æ–¥–∞ –Ω–∞ –ø–æ—Å–ª–µ–¥–Ω—é—é –±—É–∫–≤—É –≥–æ—Ä–æ–¥–∞, –∫–æ—Ç–æ—Ä—ã–π —Å—ã–≥—Ä–∞–ª –∫–æ–º–ø—å—é—Ç–µ—Ä. <br /><br />
      <strong>–ù–∞–ø—Ä–∏–º–µ—Ä</strong>: –ú–æ—Å–∫–≤–∞ üëâ –ê–º—Å—Ç–µ—Ä–¥–∞–º üëâ –ú—é–Ω—Ö–µ–Ω <br /><br /> –î–ª—è –ø–æ–±–µ–¥—ã –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ –Ω–∞–±—Ä–∞—Ç—å {this.props.finalScore} –±–∞–ª–ª–æ–≤ –±—ã—Å—Ç—Ä–µ–µ –∫–æ–º–ø—å—é—Ç–µ—Ä–∞.</React.Fragment>,
      buttonText: '–ü–æ–Ω—è—Ç–Ω–æ.'
    }
  }
  
  defineRecord() {
    const matchScore = this.props.score.human;
    let bestScore = 0;
    
    if (!localStorage.getItem('bestScore')) {
      localStorage.setItem('bestScore', matchScore);
    } else {
      bestScore = Number(localStorage.getItem('bestScore'));
      
      if (matchScore > bestScore) localStorage.setItem('bestScore', matchScore);
    }
    return matchScore > bestScore;
  }
  
  defineWordEnding(num) {
    if (num % 100 >= 11 && num % 100 <= 19) return '–æ–≤';
    if (num % 10 === 1) return '';
    if (num % 10 >= 2 && num % 10 <= 4) return '–∞';
    return '–æ–≤';
  }
  
  render() {
    const gameStarted = !this.props.gameStarted || this.props.gameEnded ? 'modal_show' : '';
    const isShown = this.state.isShown ? true : false;
        
    return (
      <div className={`modal ${gameStarted}`}>
        <ModalBox className="modal-content" pose={isShown ? 'enter' : 'exit'} >
          <h2 className="modal__title">{this.getContent().title}</h2>
          <p className="modal__description">{this.getContent().description}</p>
          <button className="modal__button" type="button" onClick={this.handleClick}>{this.getContent().buttonText}
          </button>
          </ModalBox>
      </div>
    );
  }
}

export default Modal;